import Vue from 'vue'
import Router from 'vue-router'
import MyIndex from '@/pages/index'
import Product from '@/pages/product'
import Home from '@/pages/product/home'
import Hongkong from '@/pages/product/hongkong'
import Business from '@/pages/product/business'
import International from '@/pages/product/international'
import User from '@/pages/user'
// import Carbox from '@/pages/product/hongkong/cardbox'
// import P_details from '@/pages/p_details'
import test from '@/pages/test'

Vue.use(Router)

export default new Router({
  linkActiveClass:"active",
  linkExactActiveClass:"exact",
  routes: [
    {
      path: '/',
      name: 'index',
      component: MyIndex
    },
    {
      path: '/pages/product',
      name: 'product',
      redirect:"/pages/product/home",
      component: Product,
      children:[
        {
          path:"home",
          name:"home",
          component:Home
        },
        {
          path:"hongkong",
          name:"Hongkong",
          component:Hongkong,
          // children:[
          //   {
          //     path:"cardbox",
          //     name:"Carbox",
          //     component:Cardbox
          //   }
          // ]
        },
        {
          path:"international",
          name:"international",
          component:International
        },
        {
          path:"business",
          name:"Business",//注意路由路径的引号内不得里空格；否则可能渲染不成功。
          component:Business
        }
      
      ]
    },
    {
      path: '/pages/user',
      name: 'user',
      component:User
    }
    ,
    {
      path: '/pages/test',
      name: 'test',
      component: test
    }
  ]
})



