<template>
  <div class="modal_message">

    <span class="modal_message_content" v-if="$store.state.message_show"> {{$store.state.message}}</span>

  </div>
</template>
<script>
export default {
  name: "Notice",
  data() {
    return {
    message_show:false,
    }
  },mounted(){
   
  },
  props:{
    message:{
      type:String,
      default:"加载中..."
    }
  }
};
</script>
<style lang="scss">
.modal_message {
  z-index: 9999;
  position: fixed;
  text-align: center;
  width: 100%;
  top: 40%;
  .modal_message_content {
    background-color: rgba(0, 0, 0, 0.5);
    color: #fff;
    padding: 10px;
    border-radius: 10px;
    font-size: 18px;
  }
}
</style>



