<template>
  <div>
    <div class="pro_sub_tab">
      <ol>
        <router-link tag="li" :to="item.path" v-for=" item in sublist"   :key="item.id">
          <span> {{item.title}} </span>
        </router-link>
        <!-- <li class="active">
        <span>危机保障</span>
      </li> -->
      </ol>
    </div>
  </div>
</template>
<script>
export default {
  name: "Subtab",
  data() {
    return {};
  },
  props: {
    sublist: {
      type: Array,
      default: [
        {
          title: "加载中..",
          path: "../../pages/test"
        },
        {
          title: "加载中..",
          path: "../../pages/test"
        },
        {
          title: "加载中..",
          path: "../../pages/test"
        }
      ]
    }
  }
};
</script>


<style lang="scss">
.pro_sub_tab {
  background-color: #fff;
  ol {
    display: flex;
    padding: 10px 0;
    justify-content: space-evenly;
    li {
      border: 1px solid #999999;
      border-radius: 30px;
      background-color: transparent;
      font-size: 12px;
      padding: 4px 15px 4px 15px;
      color: #999999;
      &.active {
        background-color: #d1f6f4ad;
        border: 1px solid #22dcd0;
        color: #25e0b6;
      }
    }
  }
}
</style>



